<template>
  <div class="menu16">
    <div id="menu16-border">
      <div class="container-fluid" id="menu16-body" @click="openFullscreen" v-if="signIsScheduled2 == true">
        <div class="row" id="header-title-row">
          <div class="col-2" id="logo-col">
            <img src="@/assets/c36cPizzaP7408CP1080px.png" id="hr-icon" alt="" />
          </div>
          <div class="col-10" id="header-col">
            <p id="head-title" :signId="activeSign2._id">
              {{ activeSign2.title }}
            </p>
            <p id="head-subtitle">{{ activeSign2.subTitle }}</p>
          </div>
          <div class="row" id="hr-row">
            <div class="col-10 offset-2">
              <hr id="header-line" />
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-7 offset-2" v-for="menuItem in menuItemsOfTheDay2" :key="menuItem._id" id="menu-item-col">
            <div v-show="isScheduled == true || menuItem.hide == false" id="menu-item">
              <div v-show="isScheduled == true || menuItem.hide == false" id="menu-item">
                <p id="menu-item-name">{{ menuItem.order + "." + " " + menuItem.name }}</p>
                <p id="menu-item-calories">Calories: {{ menuItem.calories }}</p>
                <p id="menu-item-description" v-html="menuItem.description"></p>
                <div id="menu-item-contains-group">
                  <p id="menu-item-contains">Contains: {{ menuItem.protein + "," }} </p>
                  <p v-if="a.checked == true" id="menu-item-contains" v-for="a in menuItem.allergens" :key="a._id">
                    {{ a.allergen }},
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Loading from "@/components/Loading.vue";
  import { mapState } from "vuex";
  import { mapGetters } from "vuex";
  export default {
    name: "Menu16",
    props: ["signId"],
    data() {
      return {
        backgroundImage: "../assets/tile-bkg-teal.jpg",
        elem: document.documentElement,
        isScheduled: false,
        reRender: false,
        isLoading: true,
        domDescription: [],
        kitchenName: "",
      };
    },
    created() {
      this.checkRouter().then((a) => {
        this.$store.dispatch("getSignsByCategory", {
          category: "Pizza",
          kitchenName: this.kitchenName,
        });
        this.$store.dispatch("checkIfScheduled")
      });
    },
    mounted() {
      this.timer()
      this.timeout = setInterval(
        () => this.$store.dispatch("checkForUpdatedSign", "Pizza"),
        60000
      );
      this.$store.dispatch("checkIfScheduled")
    },
    beforeDestroy() {
      clearInterval(this.timeout);
      clearInterval(this.timer);
    },
    computed: {
      ...mapGetters([
        "scheduledMenuItems",
        "getSignTemplate",
        "scheduled",
        "signsLength",
      ]),
      ...mapState([
        "kitchenId",
        "signs",
        "activeSign2",
        "signIsScheduled2",
        "menuItemsOfTheDay2",
        "loading",
        "rerender",
      ]),
    },
    methods: {
      openFullscreen() {
        try {
          if (this.elem.requestFullscreen) {
            this.elem.requestFullscreen();
          } else if (this.elem.mozRequestFullScreen) {
            /* Firefox */
            this.elem.mozRequestFullScreen();
          } else if (this.elem.webkitRequestFullscreen) {
            /* Chrome, Safari and Opera */
            this.elem.webkitRequestFullscreen();
          } else if (this.elem.msRequestFullscreen) {
            /* IE/Edge */
            this.elem.msRequestFullscreen();
          }
        } catch (error) {
          console.log(error);
        }
      },
      async checkRouter() {
        if (this.$router.currentRoute.path == "/menu16/cafe-17c") {
          this.kitchenName = "Cafe 17C";
        } else if (this.$router.currentRoute.path == "/menu16/cafe-36") {
          this.kitchenName = "Cafe 36";
        }
      },
      timer() {
        setInterval(this.load, 1);
        setInterval(this.checkIfScheduled, 10000);
      },
      load() {
        if (this.loading == true) {
          this.isLoading = false;
        }
      },
      checkIfScheduled() {
        return this.$store.dispatch("checkIfScheduled")
      }
    },
    components: {
      Loading,
    },
  };
</script>

<style scoped>
  .menu16 {
    background: url(../../assets/tile-bkg-teal.jpg);
    background-size: 100%;
    min-width: 100vw;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
  }

  #menu16-border {
    padding: 1.25vw;
  }

  #menu16-body {
    outline: 3px solid rgb(246, 192, 14);
    min-height: 95vh;
  }

  #head-title {
    color: rgb(246, 192, 14);
    text-align: left;
  }

  #head-title,
  #head-subtitle {
    display: inline;
  }

  #head-subtitle {
    font-family: "PT Sans Narrow", sans-serif;
    font-size: 4vw;
    margin-left: 55vw;
    margin-bottom: -10px;
    /* font-weight: bold; */
  }

  #head-title {
    font-size: 5vw;
    font-family: "PT Sans Narrow", sans-serif;
    font-style: bold;
    margin-bottom: 1%;
    margin-top: 2%;
    padding-top: 50px;
  }

  #logo-cal {
    padding: 0px;
  }

  #hr-row {
    height: 100%;
    width: 100%;
    padding-bottom: 20px;
  }

  #hr-icon {
    max-width: 25vw;
    max-height: 25vh;
    padding: 0px;
  }

  #header-col {
    padding: 0px;
    display: flex;
    align-items: baseline;
  }

  #header-line {
    height: 0px;
    margin-top: -25px;
    margin-bottom: 10px;
    border: 0;
    border-top: 3px solid whitesmoke;
  }

  #menu-item-col {
    /* margin-bottom: -40px; */
    padding-bottom: 10px;
  }

  #menu-item {
    font-family: "Open Sans", sans-serif;
    margin: 0.5vw;
    padding: 0.5vw;
    max-width: 100%;
    max-height: 100%;

  }

  #menu-item-name {
    font-size: 1.75vw;
    color: rgb(246, 192, 14);
    display: flex;
    justify-content: flex-start;
    margin: 0px;
  }

  #menu-item-calories {
    font-size: 1.25vw;
    color: whitesmoke;
    font-style: bold;
    display: flex;
    justify-content: flex-start;
    margin: 0px;

  }

  #menu-item-description {
    font-size: 1.25vw;
    display: flex;
    justify-content: flex-start;
    margin-top: 10px;
    margin-bottom: -10px;
    text-align: left;
  }

  #menu-item-contains {
    font-size: 1vw;
    margin: 0px;
    display: inline-flex;
    font-weight: bold;
    text-transform: uppercase;
  }

  #menu-item-contains-group {
    margin: 0px;
    display: flex;
    justify-content: flex-start;
    margin-bottom: -30px;
  }
</style>